<template>
  <div class="basic-info">
    <h5 class="title">1. 基本信息</h5>
    <div class="info-list">
      <div class="info-message">
        <h6 class="title">1.1 企业信息<span>契约锁权威认证</span></h6>
        <div class="message-list">
          <div class="item">
            <span class="label">企业名称</span>
            <span class="value">{{ enterpriseInfo?.companyName }}</span>
          </div>
          <div class="item">
            <span class="label">所属行业</span>
            <span class="value">{{ enterpriseInfo?.industry }}</span>
          </div>
          <div class="item">
            <span class="label">营业执照代码</span>
            <span class="value">{{ enterpriseInfo?.businessLicenseCode }}</span>
          </div>
          <div class="item">
            <span class="label">法定代表人</span>
            <span class="value">{{ enterpriseInfo?.legalName }}</span>
          </div>
          <div class="item">
            <span class="label">联系电话</span>
            <span class="value">{{ enterpriseInfo?.companyPhone }}</span>
          </div>
        </div>
      </div>
      <div class="license-picture">
        <img
          v-for="(item, index) in businessLicense"
          :key="index"
          :src="item"
          @click="previewImage('营业执照', businessLicense, index)"
          alt=""
        />
      </div>
    </div>
    <div class="info-list">
      <div class="info-message">
        <h6 class="title">1.2 法人信息<span>契约锁实名认证</span></h6>
        <div class="message-list">
          <div class="item">
            <span class="label">法人姓名</span>
            <span class="value">{{
              strExtract(legalPersonInfo?.legalName, "*", 1)
            }}</span>
          </div>
          <div class="item">
            <span class="label">法人身份证</span>
            <span class="value">{{
              strExtract(legalPersonInfo?.legalCardCode, "*", 6)
            }}</span>
          </div>
          <div class="item">
            <span class="label">法人联系方式</span>
            <span class="value">
              {{ strExtract(legalPersonInfo?.legalPhone, "*", 3, 7) }}</span
            >
          </div>
          <div class="picture-list">
            <img
              v-for="(item, index) in legalPersonInfo?.legalCard"
              :key="index"
              @click="
                previewImage('法人身份证', legalPersonInfo?.legalCard, index)
              "
              :src="item"
              alt=""
            />
          </div>
        </div>
      </div>
      <div class="info-message">
        <h6 class="title">1.3 经办人信息<span>契约锁实名认证</span></h6>
        <div class="message-list">
          <div class="item">
            <span class="label">经办人姓名</span>
            <span class="value">{{
              strExtract(operatorInfo?.operatorName, "*", 1)
            }}</span>
          </div>
          <div class="item">
            <span class="label">经办人身份证</span>
            <span class="value">{{
              strExtract(operatorInfo?.operatorCardCode, "*", 6)
            }}</span>
          </div>
          <div class="item">
            <span class="label">经办人联系方式</span>
            <span class="value">{{
              strExtract(operatorInfo?.operatorPhone, "*", 3, 7)
            }}</span>
          </div>
          <div class="picture-list">
            <img
              v-for="(item, index) in operatorInfo?.operatorCard"
              :key="index"
              @click="
                previewImage('经办人身份证', operatorInfo?.operatorCard, index)
              "
              :src="item"
              alt=""
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { computed } from "vue";
import { strExtract } from "jz-tool-lib";
const props = defineProps<{
  baseInfo: Record<string, any>;
}>();
const emits = defineEmits<{
  (e: "emitFunc", value: { title: string; imgList: string[]; index: number });
}>();
const enterpriseInfo = computed(() => props.baseInfo.enterpriseInfo);
const legalPersonInfo = computed(() => props.baseInfo.legalPersonInfo);
const operatorInfo = computed(() => props.baseInfo.operatorInfo);
const businessLicense = computed(() => props.baseInfo.businessLicense);
function previewImage(title: string, imgList: string[], index: number) {
  emits("emitFunc", {
    title,
    imgList,
    index,
  });
}
</script>

<style scoped lang="scss">
.basic-info {
  > .title {
    margin-bottom: 48px;
  }

  .info-list {
    display: flex;
    margin-bottom: 100px;
    &:last-child {
      margin-bottom: 0;
    }
    .info-message {
      flex: 0 0 570px;
      padding-right: 10px;

      .title {
        @include flex(center);
        font-size: 16px;
        font-weight: 500;
        color: #242934;
        line-height: 16px;

        span {
          padding: 5px 8px;
          font-size: 12px;
          font-weight: 500;
          color: #2ed4a9;
          line-height: 14px;
          margin-left: 12px;
          background: rgba(46, 212, 169, 0.06);
          border-radius: 4px;
        }
      }
    }
    .license-picture {
      img {
        display: block;
        width: 412px;
        height: 288px;
        object-fit: contain;
        &:last-child {
          margin-right: 0;
        }
      }
    }
    .picture-list {
      display: flex;
      flex-wrap: wrap;
      margin: 48px 0 20px;
      img {
        width: 200px;
        height: 126px;
        margin: 0 12px 12px 0;
        object-fit: contain;
        &:nth-child(2n) {
          margin-right: 0;
        }
      }
    }
    .message-list {
      .item {
        display: flex;
        margin-top: 28px;
      }
      .label,
      .value {
        font-size: 14px;
        font-weight: 400;
        line-height: 14px;
      }
      .label {
        flex: 0 0 114px;
        color: #9ca6b9;
      }

      .value {
        flex-grow: 1;
        color: #242934;
      }
    }
  }
}
</style>
